.home__main {
    display: flex;
    align-items: center;
    justify-content: center;
}
.home__main-table {
    width: 500px;
    height: 400px;
    background-color: burlywood;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.table-mazo {
    width: 60px;
    height: 80px;
    background-color: deepskyblue;
    margin-left: 15px;
    border-radius: 15px;
    border-bottom: 4px solid rgb(70, 141, 165);
    border-right: 3px solid rgb(70, 141, 165);
}
.home__main-oponent1 {
    width: 20%;
    margin-right: -30px;
    z-index: 999;
}
.home__main-oponent2 {
    width: 20%;
    margin-left: -30px;
    z-index: 999;
}
.home__main-oponent-card {
    width: 170px;
    height: 120px;
    border: 1px solid deepskyblue;
    margin-bottom: 10px;
    background-color: deepskyblue;
    border-radius: 5px;
}
.Details-oponent1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
}
.money-aponent1 {
    display: flex;
}

.Details-oponent2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
}
.money-aponent2 {
    display: flex;
}
.money {
    width: 35px;
    height: 35px;
    background-color: gold;
    border-radius: 50%;
    margin-left: 5px;
    margin-right: 5px;
}
.Details-oponent1 h2,
.Details-oponent2 h2 {
    color: white;
}